@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{box-sizing:border-box;margin:0;padding:0}body{height:100vh;width:100%;background-color:#f3f3f3;font-family:Arial,Helvetica,sans-serif;line-height:1.6;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}#logo{font-family:"Lobster",cursive;font-size:3em;color:#f66696;-webkit-text-stroke:0.1px #555}.app{background-color:#fff;min-width:520px;width:70%;border:4px solid #f66696;border-radius:1rem;padding:0.8rem;box-shadow:0 5px 20px rgba(0,0,0,0.4)}#item-form{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}#item-form input{min-width:none;max-width:50ch;height:1.6rem;border:2px silver solid;border-radius:1em;padding-left:0.5em}#item-form .submit{width:50%;height:30px;margin:1.6em 0;border-radius:1em;border:2px silver solid}#item-form .submit:hover{border-color:#f66696}.list-header{width:100%;border-collapse:collapse}.list-header th{border-bottom:#f66696 solid 1px}.list-header td{text-align:center;padding:0.2em 0}.shop-total{padding:0.2em 0;border-top:1px solid #f66696}.shop-total button{width:50%;height:30px;margin:1.6em 0;border-radius:1em;border:2px silver solid}.shop-total button:hover{border-color:#f66696}.item-btn{width:3ch;height:3ch;border:2px silver solid;border-radius:50%;margin:0 0.125rem}.add:hover{background-color:#7cff25}.minus:hover{background-color:#ffc425}.delete:hover{background-color:#ff6666}

